@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap');

body {
	background:linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5);
	font-family: 'Poppins', sans-serif;
	margin: 20px;
  }

#root{
	  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  
}

.find-player-input{
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 20px;

}

.find-player-input input{
	padding: 10px 20px;
	margin : 0 10px;
	border : 2px solid #86A8E7;
	border-radius: 0.5rem;
	width: 10rem;
	font-size: 1rem;
}

.btn{
	padding: 10px 20px;
	border: none;
	border-radius: 0.5rem;
	background-color: #86A8E7;
	color: white;
	font-size: 1rem;
	cursor: pointer;
	transition: all 0.3s ease;

}

.btn:hover{
	background-color: #7F7FD5;
}

.board{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 90vh;
	width: 90vw;
	background: white;
	/* border: 6px solid #999999; */
	box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
	border-radius: 10px;
}

.board-row:after {
	clear: both;
	content: "";
	display: table;
}

.player-info{
    width: 100%;
    /* background-color: #ffac3f; */
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.status {
	padding : 10px;
	background-color: #ffac3f;
	margin-bottom: 10px;
	border-radius: 0.5rem;
	text-align: center;
}

.board-row:after {
	clear: both;
	content: "";
	display: table;
}

.square {
	background-color: #ffac3f;
	border: 1px solid #999999;
	float: left;
	font-size: 40px;
	height: 80px;
	margin-right: 8px;
	margin-top: 8px;
	border-radius: 0.5rem;
	padding: 0;
	text-align: center;
	width: 80px;
}

button:hover {
	cursor: pointer;
}

.status, .restart {
	margin-top: 10px;
}
  
  /** button styles **/
.restart {
	padding: 12px 15px;
	color: #fff;
	background-color: #007bff;
	border-color: #007bff;
	border-radius: 0.5rem;
	transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
  
.restart:active {
	color: #fff;
	background-color: #0062cc;
	border-color: #005cbf;
}
  
.restart:hover {
	color: #fff;
	background-color: #0069d9;
	border-color: #0062cc;
}
  
.restart:focus {
	box-shadow: 0 0 0 0.2rem rgb(0 123 255 / 50%);
}